import React from 'react';
import './index.css';
import { Carousel, WingBlank } from 'antd-mobile';
import SongList from './songList';
import { getBanner } from '../../../../api/banner';

export default class recommend extends React.Component {
    state = {
        bannerList: null
    };
    componentDidMount() {
        this.getBanners()
        // this.$lazyLoading(this.recommend)
    }
    async getBanners() {
        try {
            let res = await getBanner({
                type: 1
            });
            this.setState({
                bannerList: res.banners
            });
        } catch (err) {
            console.log(err);
        }
    }
    
    render() {
        const {bannerList} = this.state
        return (
            <div className="recommend" id="recommend" ref={val => this.recommend = val}>
                <div >
                    <div className="bg"></div>
                    <div className="carousel" onTouchMove={(e) => e.stopPropagation()}>
                        <WingBlank>
                           {bannerList&& <Carousel
                                autoplay={true}
                                infinite
                            >
                                {bannerList.map((item, index) => (
                                    <div
                                        key={index}
                                        style={{
                                            display: 'inline-block',
                                            width: '100%',
                                            height: '40vw'
                                        }}
                                    >
                                        <img
                                            src={item.imageUrl}
                                            alt=""
                                            style={{
                                                width: '100%',
                                                height: '100%',
                                                verticalAlign: 'top'
                                            }}
                                            onLoad={() => {
                                                // fire window resize event to change height
                                                window.dispatchEvent(
                                                    new Event('resize')
                                                );
                                                this.setState({
                                                    imgHeight: 'auto'
                                                });
                                            }}
                                        />
                                    </div>
                                ))}
                            </Carousel>}
                        </WingBlank>
                    </div>
                    <div className="song-list">
                        <SongList isLoad={this.props.isLoad}/>
                    </div>
                </div>
            </div>
        );
    }
}
