import {SET_MUC_DATA , SET_SEARCH_DATA ,SET_API} from './actionType';

export const setMucData = (data) => {
    return {
        type:SET_MUC_DATA,
        data
    }
}

export const setSearchData = (data) => {
    return {
        type:SET_SEARCH_DATA,
        data
    }
}
export const setApi = (data) => {
    return {
        type:SET_API,
        data
    }
}

export const addAsync = data => {
    return dispatch => {
        setTimeout(() => {
            dispatch(setMucData(data))
        }, 2000);
    }
}
