import axios from 'axios';
import store from '../redux';
import {setApi} from '../redux/actionCreator';
import {dispose} from '../utils/apiCache';

var request = axios.create({
    baseURL: 'https://autumnfish.cn'
});
//配置axios全局配置
request.defaults.transformResponse = [
    data => {
        return data;
    }
];


// 添加请求拦截器
request.interceptors.request.use(
    config => {
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);

// 添加响应拦截器
request.interceptors.response.use(
    response => {
        let data = JSON.parse(response.data)
        let apiUrl = response.config.url.split('https://autumnfish.cn')[1]
        apiUrl += dispose(response.config.params)
        store.setApi.dispatch(setApi({[apiUrl]:data}))
        return data;
    },
    err => {
        return Promise.reject(err);
    }
);

export default request
