import React from 'react'
import './index.scss'
import { List, Grid } from 'antd-mobile'
import { getRanking } from '../../../../api/ranking'
import { getDetails } from '../../../../api/song_menu'
import { withRouter } from 'react-router-dom'
const Item = List.Item
const Brief = Item.Brief

class ranking extends React.Component {
  state = {
    data: [1, 2, 3, 4, 5],
    authorityList: [],
    globalList: []
  }
  componentDidMount() {
    this.getRanking()
  }
  async getRanking() {
    try {
      let res = await getRanking()
      this.setState({ authorityList: res.list.slice(0, 4) }, () => {
        this.state.authorityList.forEach((item, index) => {
          this.getSongMenu(item.id, index)
        })
      })
      this.setState({ globalList: res.list.slice(4, res.list.length) })
    } catch (err) {
      console.log(err)
    }
  }
  async getSongMenu(id, index) {
    try {
      let res = await getDetails(id)
      let arr = this.state.authorityList
      arr[index].tags = res.playlist.tracks.slice(0, 3)
      this.setState({ authorityList: arr })
    } catch (err) {
      console.log(err)
    }
  }

  songMenu(item) {
    this.props.history.push('/songMenu/' + item.id)
  }
  render() {
    return (
      <div className='ranking'>
        <div className='box'>
          <div className='authority'>
            <div className='title'>官方榜</div>
            <List>
              {this.state.authorityList.map((item, index) => {
                return (
                  <Item
                    key={index}
                    thumb={<img src="" alt='' data-url={item.coverImgUrl}/>}
                    onClick={() => {
                      this.songMenu(item)
                    }}
                  >
                    {this.state.authorityList[index].tags.map((item, index) => {
                      return (
                        <Brief key={index}>
                          {index + 1}. {item.name}
                        </Brief>
                      )
                    })}
                  </Item>
                )
              })}
            </List>
          </div>
          <div className='global'>
            <div className='title'>全球榜</div>
            <Grid
              hasLine={false}
              data={this.state.globalList}
              columnNum={3}
              renderItem={item => (
                <div
                  onClick={() => {
                    this.songMenu(item)
                  }}
                >
                  <img
                    src=""
                    style={{
                      width: '100%',
                      height: '100%',
                      borderRadius: '5px'
                    }}
                    data-url={item.coverImgUrl}
                    alt=''
                  />
                  <div
                    style={{
                      color: '#888',
                      fontSize: '14px',
                      marginTop: '12px'
                    }}
                  >
                    <span>{item.updateFrequency}</span>
                  </div>
                </div>
              )}
            />
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(ranking)
