import request from '../utils/request'
import { apiCache, dispose } from '../utils/apiCache'

function getHotSongMenu() {
  return request({
    method: 'get',
    url: '/personalized',
    params: {
      t: Date.now()
    }
  })
}

function getDetails(id) {
  return apiCache(() => {
    return request({
      method: 'get',
      url: '/playlist/detail',
      params: {
        id,
        t: Date.now()
      }
    })
  }, `/playlist/detail${dispose({ id })}`)
}

function getPlaylist(id) {
  return apiCache(() => {
    return request({
      method: 'get',
      url: '/simi/playlist',
      params: {
        id,
        t: Date.now()
      }
    })
  }, `/simi/playlist${dispose({ id })}`)
}

export { getHotSongMenu, getDetails, getPlaylist }
