function myToast(msg, tiem = 3000, type) {
    let popup = document.createElement('div');
    popup.innerText = msg;
    document.body.append(popup);
    popup.style.position = 'fixed';
    popup.style.opacity = 0;
    popup.style.left = '50%';
    popup.style.textAlign='center';
    switch (type) {
        case 'top':
            popup.style.top = '-5%';
            popup.style.transform = 'translateX(-50%)';
            setTimeout(() => {
                popup.style.top = '2%';
            }, 0);
            break;
        case 'bottom':
            popup.style.top = '110%';
            popup.style.transform = 'translateX(-50%)';
            setTimeout(() => {
                popup.style.top = '90%';
            }, 0);
            break;
        default:
            popup.style.top = '50%';
            popup.style.transform = 'translateX(-50%) translateY(-50%)';
            break;
    }
    popup.style.zIndex = 99999999999;
    popup.style.transition = 'all .5s';
    popup.style.padding = '8px 10px';
    popup.style.backgroundColor = 'rgba(0,0,0,.7)';
    popup.style.color = '#fff';
    popup.style.borderRadius = '5px';
    setTimeout(() => {
        popup.style.opacity = 1;
    }, 0);
    setTimeout(() => {
        popup.style.opacity = 0;
    }, tiem);
    setTimeout(() => {
        document.body.removeChild(popup);
    }, tiem + 500);
}
export default myToast;
