import request from '../utils/request'
import { apiCache, dispose } from '../utils/apiCache'

function singerType({ cat, initial }) {
  return apiCache(() => {
    return request({
      method: 'get',
      url: '/artist/list',
      params: {
        cat,
        initial,
        t: Date.now()
      }
    })
  }, `/artist/list${dispose({ cat, initial })}`)
}

function getSongList({ id }) {
  return apiCache(() => {
    return request({
      method: 'get',
      url: '/artists',
      params: {
        id,
        t: Date.now()
      }
    })
  }, `/artists${dispose({ id })}`)
}

function getSingerDesc(id) {
  return apiCache(() => {
    return request({
      method: 'get',
      url: '/artist/album',
      params: {
        id,
        t: Date.now()
      }
    })
  }, `/artist/album${dispose({ id })}`)
}

export { singerType, getSongList, getSingerDesc }
