import React from 'react'
import './index.scss'
import { searchMusic, getSearchHot } from '../../api/search'
import { getSingerDesc } from '../../api/singer'
import { getPlaylist } from '../../api/song_menu'
import { List, Tag } from 'antd-mobile'
import store from '../../redux'
import { setMucData, setSearchData } from '../../redux/actionCreator'
const Item = List.Item
const Brief = Item.Brief
let tiemId = null

export default class search extends React.Component {
  state = {
    boxStyle: {},
    key: '',
    searchList: [],
    singerData: {},
    playlists: [],
    searchHot: JSON.parse(window.localStorage.getItem('searchHot') || '[]')
  }
  componentDidMount() {
    setTimeout(() => {
      const key = store.searchData.getState()
      this.setState({
        boxStyle: {
          transform: 'translateX(0)'
        },
        key
      })
      if (key.trim().length > 0) this.searchMusic(key.trim())
    }, 0)

    let input = document.getElementById('input')
    input.focus()
    setTimeout(() => {
      this.search.style.backgroundColor = '#f3f3f4'
    }, 300)
    if (this.state.searchHot.length === 0) this.getSearchHot()
  }
  // 获取搜索热词
  async getSearchHot() {
    try {
      let res = await getSearchHot()
      this.setState({ searchHot: res.result.hots })
    } catch (err) {
      console.log(err)
    }
  }
  // 当搜索框值改变时
  onChange(e) {
    let key = e.target.value
    this.setState({ key })
    if (tiemId) {
      clearInterval(tiemId)
    }
    tiemId = setTimeout(() => {
      clearInterval(tiemId)
      if (key.trim().length > 0) {
        this.searchMusic(key.trim())
      } else {
        this.setState({ searchList: [] })
      }
    }, 500)
  }
  // 获取搜索内容
  async searchMusic(key) {
    store.searchData.dispatch(setSearchData(key))
    try {
      let res = await searchMusic(key)
      this.getPlaylist(res.result.songs[0].id)
      this.getSingerDesc(res.result.songs[0].artists[0].id)
      this.setState({ searchList: res.result.songs })
    } catch (err) {
      console.log(err)
    }
  }
  // 获取歌手详情
  async getSingerDesc(id) {
    try {
      let res = await getSingerDesc(id)
      this.setState({ singerData: res.artist })
      return res.artist
    } catch (err) {
      console.log(err)
    }
  }
  // 获取相似歌单
  async getPlaylist(id) {
    try {
      let res = await getPlaylist(id)
      this.setState({ playlists: res.playlists })
    } catch (err) {
      console.log(err)
    }
  }
  // 播放歌曲
  play(list, index, e) {
    try {
      let arr = []
      list.forEach((item, _index) => {
        arr.push({
          id: item.id,
          name: item.name,
          arName: item.artists[0].name,
          artId: item.artists[Math.floor(Math.random() * item.artists.length)].id
        })
      })
      store.musicUrl.dispatch(setMucData({ list: arr, index, even: e }))
    } catch (err) {
      console.log(err)
    }
  }
  addsearchHot() {
    let key = this.state.key
    let list = this.state.searchHot
    let keyList = this.state.searchHot.map(item => {
      return item.first
    })
    if (!keyList.includes(key)) {
      list.pop()
    } else {
      list.splice(keyList.indexOf(key), 1)
    }
    list.unshift({ first: key })
    window.localStorage.setItem('searchHot', JSON.stringify(list))
    this.setState({ searchHot: list })
  }
  render() {
    return (
      <div className='search' ref={val => (this.search = val)}>
        <div className='box' ref={val => (this.box = val)} style={this.state.boxStyle}>
          {this.state.searchList.length > 0 && (
            <div className='content'>
              <div className='singer'>
                <div className='title'>相关歌手</div>
                <Item
                  thumb={this.state.singerData.picUrl}
                  onClick={() => {
                    this.addsearchHot()
                    this.props.history.push(`/singerDetails/${this.state.singerData.id}`)
                  }}
                >
                  {this.state.singerData.name}
                </Item>
              </div>
              <div className='songMenuList'>
                <div className='title'>相关歌单</div>
                <List>
                  {this.state.playlists.map((item, index) => {
                    return (
                      <Item
                        key={index}
                        thumb={item.coverImgUrl}
                        onClick={() => {
                          this.addsearchHot()
                          this.props.history.push('/songMenu/' + item.id)
                        }}
                      >
                        {item.name}
                      </Item>
                    )
                  })}
                </List>
              </div>
              <div className='songList'>
                <List>
                  {this.state.searchList.map((item, index) => {
                    return (
                      <Item
                        onClick={e => {
                          this.addsearchHot()
                          this.play(this.state.searchList, index, e)
                        }}
                        key={index}
                      >
                        {item.name}
                        <Brief>
                          {item.artists[0].name} - {item.album.name}
                        </Brief>
                      </Item>
                    )
                  })}
                </List>
              </div>
            </div>
          )}

          {this.state.searchList.length === 0 && (
            <div className='hotKey'>
              <div className='title'>热门搜索</div>
              <div className='content'>
                {this.state.searchHot.map((item, index) => {
                  return (
                    <Tag data-seed='logId' key={index}>
                      <div
                        onClick={() => {
                          this.searchMusic(item.first)
                          this.input.value = item.first
                          this.setState({
                            key: item.first
                          })
                        }}
                      >
                        {item.first}
                      </div>
                    </Tag>
                  )
                })}
              </div>
            </div>
          )}
        </div>
        {/* 搜索条 */}
        <div className='searchBar' style={this.state.boxStyle}>
          <i
            className='iconfont icon-xiangzuojiantou'
            onClick={() => {
              this.search.style.backgroundColor = ''
              this.setState({
                boxStyle: { transform: 'translateX(100%)' }
              })
              setTimeout(() => {
                this.props.history.go(-1)
              }, 300)
            }}
          />
          <input
            type='text'
            id='input'
            placeholder='搜索歌曲、歌手、专辑'
            ref={val => (this.input = val)}
            value={this.state.key}
            onChange={e => this.onChange(e)}
          />
          {this.state.key.trim().length > 0 && (
            <i
              className='iconfont icon-guanbi'
              onClick={() => {
                this.input.value = ''
                this.setState({ key: '' })
                this.setState({ searchList: [] })
              }}
            />
          )}
        </div>
      </div>
    )
  }
}
