import store from '../redux'

export function apiCache(fun, url) {
  let apiData = store.setApi.getState()
  if (url in apiData) {
    return Promise.resolve(apiData[url])
  } else {
    return fun()
  }
}

export function dispose(obj) {
  let apiUrl = ''
  for (let key in obj) {
    if (key !== 't') {
        apiUrl += `/${key}:${JSON.stringify(obj[key])}`
    }
  }
  return apiUrl
}

