import {SET_MUC_DATA , SET_SEARCH_DATA ,SET_API} from './actionType';

function musicUrl(data, action) {
    switch (action.type) {
        case SET_MUC_DATA:
            return data = action.data;
        default:
            return data;
    }
}

function searchData(data = '', action) {
    switch (action.type) {
        case SET_SEARCH_DATA:
            return data = action.data;
        default:
            return data;
    }
}

const apiData = JSON.parse(window.localStorage.getItem('apiData') || '{}')
function setApi(data = apiData, action) {
    switch (action.type) {
        case SET_API:
            let tempObj = {...data}
            tempObj = {...tempObj,...action.data}
            return tempObj;
        default:
            return data;
    }
}

export { musicUrl ,searchData ,setApi};

