import React from 'react'
import throttle from './throttle'


export default class LazyLoading extends React.Component {
  state = {
    placeholder:this.props.src || 'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=1897943806,295048940&fm=26&gp=0.jpg',
    windowHei : window.innerHeight + 100
  }
  componentDidMount() {
    this.init()
  }

  init() {
    let ele = this.box.parentNode
    this.getNodeLise(ele)
  }

  async getNodeLise(ele) {
    const windowHei = this.state.windowHei
    let imgArr = ele.querySelectorAll('[data-url]')
    if (imgArr.length === 0) {
      await setTimeout(() => {
         this.getNodeLise(ele)
      }, 500)
    } else {
      this.isLoad(ele ,windowHei)
      imgArr.forEach(item => {
        if (item.getAttribute('src') === '') {
          item.setAttribute('src', this.state.placeholder)
        }
      })
      ele.addEventListener('scroll', () => {
        throttle(this.isLoad, 200, ele ,windowHei)
      })
    }
  }

  isLoad(ele,windowHei) {
    let imgArr = ele.querySelectorAll('[data-url]')
    imgArr.forEach(item => {
      if (item.y < windowHei) {
        item.setAttribute('src', item.getAttribute('data-url'))
        item.removeAttribute('data-url')
      }
    })
  }
  render() {
    return <div ref={val => (this.box = val)}>{this.props.children}</div>
  }
}
