import { createStore, applyMiddleware, compose } from 'redux';

import thunk from 'redux-thunk';
import { musicUrl ,searchData ,setApi} from './reducers';

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = {
    musicUrl: createStore(musicUrl, composeEnhancers(applyMiddleware(thunk))),
    searchData: createStore(searchData, composeEnhancers(applyMiddleware(thunk))),
    setApi: createStore(setApi, composeEnhancers(applyMiddleware(thunk))),
};

export default store;