import React from 'react'
import { Route } from 'react-router-dom'
const renderRoutes = (routes, extraProps = {}, switchProps = {}) =>
  routes ? (
    <>
      {routes.map((route, i) => (
        <Route
          key={route.key || i}
          path={route.path}
          exact={route.exact}
          strict={route.strict}
          render={props => {
            return <route.component {...props} {...extraProps} route={route} />
          }}
        />
      ))}
    </>
  ) : null
export default renderRoutes
