import request from '../utils/request'
import { apiCache, dispose } from '../utils/apiCache'

function getMusicUrl(id) {
  return apiCache(() => {
    return request({
      method: 'get',
      url: '/song/url',
      params: {
        id,
        t: Date.now()
      }
    })
  }, `/song/url${dispose({ id })}`)
}
function getMusicLyc(id) {
  return apiCache(() => {
    return request({
      method: 'get',
      url: '/lyric',
      params: {
        id,
        t: Date.now()
      }
    })
  }, `/lyric${dispose({ id })}`)
}

export { getMusicUrl, getMusicLyc }
