import SongMenu from '../view/songMenu'
import SingerDetails from '../view/singerDetails'
import Search from '../view/search'
import Home from '../view/home'

const routes = [
    {
        path: '/',
        component: Home
    },
    {
        path: '/search',
        component: Search
    },
    {
        path: '/songMenu/:id',
        component: SongMenu
    },
    {
        path: '/singerDetails/:id',
        component: SingerDetails
    },
]


export default routes