function playAnimation(e) {
    let root = document.getElementById('root');
    let ico = document.createElement('i');
    ico.className = 'iconfont icon-yinle animation';
    ico.style.top = e.pageY - 5 + 'px';
    ico.style.left = e.pageX - 5 + 'px';
    root.append(ico);
    setTimeout(() => {
        ico.style.top = window.screen.height - 45 + 'px';
        ico.style.left = '8%';
        ico.style.fontSize = '30px'
    }, 0);
    setTimeout(() => {
        ico.style.opacity = 0;
    },1000)
    setTimeout(() => {
        root.removeChild(ico);
    }, 2000);
}

export { playAnimation };
